/* frontend/src/components/Home.css */
.home-container {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.hero-section {
    background-color: #f4f4f4;
    padding: 4rem 2rem;
    border-radius: 8px;
    margin-bottom: 3rem;
}

.hero-section h1 {
    font-size: 3rem;
    color: #333;
    margin-bottom: 1rem;
}

.hero-section p {
    font-size: 1.2rem;
    color: #666;
    margin-bottom: 2rem;
}

.cta-button {
    background-color: #007bff;
    color: white;
    padding: 1rem 2rem;
    border: none;
    border-radius: 5px;
    font-size: 1.1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.cta-button:hover {
    background-color: #0056b3;
}

.intro-section {
    padding: 2rem 0;
    text-align: left;
}

.intro-section h2 {
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 1.5rem;
    text-align: center;
}

.intro-section p {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #555;
}

/* Basic responsiveness for Home */
@media (max-width: 768px) {
    .hero-section h1 {
        font-size: 2.2rem;
    }

    .hero-section p {
        font-size: 1rem;
    }

    .cta-button {
        padding: 0.8rem 1.5rem;
        font-size: 1rem;
    }

    .intro-section h2 {
        font-size: 2rem;
    }
}